import React from "react"
import PropTypes from "prop-types"

// Data
import data from "../../data/header.json"
import services from "../../data/services.json"

// Components
import { Link } from "gatsby"
import LogoLight from "../../svg/aristo-light.svg"
import LogoDark from "../../svg/aristo-Dark.svg"
import { StyledHeader, StyledContainer, StyledNav, StyledLogo } from "./styles"

export const Header = ({ siteMeta = {}, pageContext = {} }) => {
  const { nav } = data
  const { template } = pageContext

  // Add services sub links
  nav.services.children = Object.values(services.data).map(page => {
    return {
      label: page.title,
      src: page.path,
    }
  })

  // Recursively Build Nav
  const createNavSection = section => (
    <ul>
      {section.map((link, index) => (
        <li key={index}>
          <Link to={link.src}>{link.label}</Link>
          {/* {link.children &&
            link.children.length > 0 &&
            createNavSection(link.children)} */}
        </li>
      ))}
    </ul>
  )
  const darkTheme = template !== "home"
  return (
    <StyledHeader darkTheme={darkTheme}>
      <StyledContainer>
        <StyledLogo>{darkTheme ? <LogoDark /> : <LogoLight />}</StyledLogo>
        <StyledNav darkTheme={darkTheme}>
          {createNavSection(Object.values(nav))}
        </StyledNav>
      </StyledContainer>
    </StyledHeader>
  )
}

Header.propTypes = {
  siteMeta: PropTypes.object,
  pageContext: PropTypes.object,
}
