import React from "react"
import PropTypes from "prop-types"

import { Button } from "../button"
import { Image } from "../image"

export const ServicePage = ({ pageContext = {} }) => {
  return (
    <>
      <div>
        <div className="hero">
          <div style={{ width: "250px" }}>
            <Image src={pageContext.heroImagePath} />
          </div>
          <h3>{pageContext.title}</h3>
          <h2>{pageContext.heroText || pageContext.title}</h2>
          <p>{pageContext.description}</p>
        </div>

        {pageContext.products && (
          <div className="product-list">
            {pageContext.products.map((product, index) => (
              <div key={index} className="product-card">
                <h3>{product.title}</h3>
                <p>{product.description}</p>
                <Button
                  variant="link"
                  to="/contact"
                >{`Schedule a meeting for ${product.title.toLowerCase()}`}</Button>
              </div>
            ))}
          </div>
        )}

        {pageContext.moreInfo && (
          <div className="statement">
            <h2>{pageContext.moreInfo.title}</h2>
            <p>{pageContext.moreInfo.description}</p>
            <Button to={pageContext.moreInfo.cta.src}>
              {pageContext.moreInfo.cta.label}
            </Button>
          </div>
        )}

        {pageContext.disclaimer && <p>{pageContext.disclaimer}</p>}

        <div className="service-slider"></div>

        {pageContext.endHero && (
          <div className="endHero" style={{ width: "250px" }}>
            <Image src={pageContext.endHero.imagePath} />
            <h3>{pageContext.endHero.title}</h3>
            <p>{pageContext.endHero.description}</p>
            <Button to={pageContext.endHero.cta.src}>
              {pageContext.endHero.cta.label}
            </Button>
          </div>
        )}
      </div>
    </>
  )
}

ServicePage.propTypes = {
  pageContext: PropTypes.object.isRequired,
}
