import styled, { css } from "styled-components"

import { Link } from "gatsby"
import { colors } from "../../constants/theme"

const BaseStyles = css`
  /* Base styles */
  display: inline-flex;
  align-items: center;
  justify-content: center;
  border-radius: 4px;
  font-size: 16px;
  height: 46px;
  transition: all 200ms ease-in-out;
  font-weight: 700;

  /* Variations */
  ${props =>
    props.$variant !== "link"
      ? `
		padding: 0 2rem;
		border-width: 2px;
		border-style: solid;
		border-color: ${props.$reversed ? "#fff" : colors.brand};
		color: ${
      props.$variant === "outline"
        ? props.$reversed
          ? "#fff"
          : colors.brand
        : props.$reversed
        ? colors.brand
        : "#fff"
    };
		background-color: ${
      props.$variant === "outline"
        ? "transparent"
        : props.$reversed
        ? "#fff"
        : colors.brand
    };
	`
      : `
		padding: 0 6px;
		color: ${props.$reversed ? "#fff" : colors.brand};
	`}

  &:hover {
    ${props =>
      props.$variant === "solid"
        ? `
			background-color: ${props.$reversed ? "#dbf0f3" : colors.brandDark};
			border-color: ${props.$reversed ? "#dbf0f3" : colors.brandDark};
		`
        : `
			background-color: rgba(3, 169, 244, 0.2);
		`}
  }

  &:focus {
    outline: none;
    box-shadow: 0 0 0 2px #607d8b;
  }

  &:active {
    transform: scale(0.95);
  }

  /* Gutter */
  margin-top: ${props =>
    props.gutter && props.gutter.top ? props.gutter.top : undefined};
  margin-bottom: ${props =>
    props.gutter && props.gutter.bottom ? props.gutter.bottom : undefined};
  margin-left: ${props =>
    props.gutter && props.gutter.left ? props.gutter.left : undefined};
  margin-right: ${props =>
    props.gutter && props.gutter.right ? props.gutter.right : undefined};
`

export const StyledButton = styled.button`
  /* Resets */
  border: none;
  margin: 0;
  width: auto;
  overflow: visible;
  background: transparent;
  cursor: pointer;
  font: inherit;
  line-height: normal;
  user-select: none;
  -webkit-font-smoothing: inherit;
  -moz-osx-font-smoothing: inherit;
  -webkit-appearance: none;
  /* Styles */
  ${BaseStyles}
`

export const StyledLink = styled(Link)`
  ${BaseStyles}
`

export const StyledArrowIcon = styled.svg`
  margin-left: 4px;
  width: 14px;
  height: 14px;
  display: block;
  fill: currentColor;
`
