import styled from "styled-components"

export const StyledContainer = styled.section`
  width: 100%;
  margin-right: auto;
  margin-left: auto;
  padding: 0 24px;
  ${props => (!props.fluid ? `max-width: 1280px;` : undefined)}

  /* Gutter */
  margin-top: ${props =>
    props.gutter && props.gutter.top ? props.gutter.top : undefined};
  margin-bottom: ${props =>
    props.gutter && props.gutter.bottom ? props.gutter.bottom : undefined};
`
