import styled from "styled-components"

export const StyledStatement = styled.div`
  display: flex;
  margin-right: auto;
  margin-left: auto;
  ${props => (props.maxWidth ? `max-width: ${props.maxWidth}` : undefined)};

  /* Gutter */
  margin-top: ${props =>
    props.gutter && props.gutter.top ? props.gutter.top : undefined};
  margin-bottom: ${props =>
    props.gutter && props.gutter.bottom ? props.gutter.bottom : undefined};
`

export const StyledHeadline = styled.h2`
  font-weight: bold;
  font-size: ${props => (props.condensed ? "24px" : "44px")};
  width: ${props => (props.condensed ? "200px" : "350px")};
`

export const StyledBody = styled.p``